import axios from "@/plugins/axios";

export default {
  namespaced: true,

  state: {
    student: null,
    token: localStorage.getItem("student_token"),
    initialized: false, // ðŸ‘ˆ IMPORTANT
  },

  getters: {
    // âœ… Auth depends on student, not just token
    isAuthenticated: (state) => !!state.student,
    student: (state) => state.student,
    studentName: (state) => state.student?.name || "",
    isStudent: (state) => state.student?.role === "student",
    isReady: (state) => state.initialized,
  },

  mutations: {
    SET_STUDENT(state, student) {
      state.student = student
        ? { ...student, role: "student" } // ðŸ‘ˆ enforce role
        : null;
    },

    SET_TOKEN(state, token) {
      state.token = token;

      if (token) {
        axios.defaults.headers.common.Authorization = `Bearer ${token}`;
      } else {
        delete axios.defaults.headers.common.Authorization;
      }
    },

    SET_INITIALIZED(state, value) {
      state.initialized = value;
    },

    LOGOUT(state) {
      state.student = null;
      state.token = null;
      state.initialized = true;
      delete axios.defaults.headers.common.Authorization;
    },
  },

  actions: {
    async loginStudent({ commit, dispatch }, credentials) {
      const res = await axios.post("/student/login", credentials);

      const token = res.data.token;
      localStorage.setItem("student_token", token);
      commit("SET_TOKEN", token);

      await dispatch("fetchStudentProfile");

      return true;
    },

    async fetchStudentProfile({ commit, state }) {
      try {
        if (!state.token) {
          commit("SET_INITIALIZED", true);
          return;
        }

        const res = await axios.get("/student/me");
        commit("SET_STUDENT", res.data.data);
      } catch (err) {
        console.error("Fetch profile failed:", err);
        localStorage.removeItem("student_token");
        commit("LOGOUT");
      } finally {
        commit("SET_INITIALIZED", true);
      }
    },

    logoutStudent({ commit }) {
      localStorage.removeItem("student_token");
      commit("LOGOUT");
    },
  },
};
