<template>
  <v-app>
    <!-- ðŸ”„ Loading -->
    <v-container
      v-if="!isReady"
      class="fill-height d-flex justify-center align-center"
    >
      <v-progress-circular indeterminate color="primary" />
    </v-container>

    <!-- ðŸ” Login -->
    <LoginPage
      v-else-if="!isAuthenticated"
      @login-success="onLoginSuccess"
    />

    <!-- ðŸŽ“ Student Portal -->
    <template v-else-if="isStudent">
      <v-app-bar color="primary" dark>
        <v-toolbar-title>Student Portal</v-toolbar-title>
        <v-spacer />
        <v-btn text @click="logout">Logout</v-btn>
      </v-app-bar>

      <v-main>
        <v-container fluid>
          <StudentPortal />
        </v-container>
      </v-main>
    </template>

    <!-- âŒ Invalid Role (should never happen now) -->
    <v-container
      v-else
      class="fill-height d-flex justify-center align-center"
    >
      <v-card>
        <v-card-title>Error: Invalid user role</v-card-title>
      </v-card>
    </v-container>
  </v-app>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
import LoginPage from "./views/LoginPage.vue";
import StudentPortal from "./views/StudentPortal.vue";

export default {
  name: "App",

  components: {
    LoginPage,
    StudentPortal,
  },

  computed: {
    ...mapGetters("auth", [
      "isAuthenticated",
      "isStudent",
      "isReady",
    ]),
  },

  methods: {
    ...mapActions("auth", ["logoutStudent"]),

    onLoginSuccess() {
      // optional hook
      console.log("Student logged in");
    },

    logout() {
      this.logoutStudent();
      location.reload(); // hard reset UI state
    },
  },
};
</script>

<style scoped>
.fill-height {
  min-height: 100vh;
}
</style>
